import { singletonClass } from "../../submodules/toolkits.js/src/classDefiner";
import { dynInvokeLink } from "../../submodules/toolkits.js/src/domKits";
import tip from "../../submodules/toolkits.js/src/tip/tip";

function onContentClick() {
    if (this.download) {
        dynInvokeLink(this.download, {
            download: this.download.substr(this.download.lastIndexOf("/") + 1)
        });
    } else if (this.href) {
        window.open(this.href, "blank");
    }
}

const WelcomeApp = singletonClass("WelcomeApp", {
    $constructor() {
        this.$rootElement = document.getElementById("welcome-app");
    },

    async refresh() {
        try {
            const contentBox = this.$rootElement.querySelector(".welcome-app-content-box");
            contentBox.innerHTML = "";

            let resp = await fetch("./home-content.json");
            resp = await resp.json();
            resp.forEach(item => {
                let itemDiv = document.createElement("div");
                itemDiv.setAttribute("class", "welcome-app-content");
                itemDiv.setAttribute("d-content-desc", item.desc || "");
                itemDiv.insertAdjacentHTML("afterbegin", `<img src="${item.image}" /><div>${item.title}</div>`);
                itemDiv.addEventListener("click", onContentClick.bind(item));
                contentBox.appendChild(itemDiv);
            });
        } catch (error) {
            tip("Fail in fresh home contents", { type: "error", timeout: 1700, closable: true });
            console.error("Fail in fresh home contents", error);
        }
    },
    
    hide() {
        this.$rootElement && this.$rootElement.remove();
    },
    
    show() {
        const container = document.querySelector(".--app-container");
        this.$rootElement && container && (container.innerHTML = "", container.appendChild(this.$rootElement));
    }
});

export default WelcomeApp;
