import { singletonClass } from "../../submodules/toolkits.js/src/classDefiner";

const WaitTip = singletonClass("WaitTip", {
    $constructor() {
        this.$rootElement = document.getElementById("waiting-box");
        this.close();
        this.$rootElement.style.display = "";
    },

    show(_text) {
        let obj = this.$rootElement.querySelector("#waiting-text");
        obj.textContent = (_text || "");
        (!this.$rootElement.isConnected) && document.body.appendChild(this.$rootElement);
        return this;
    },

    close() {
        this.$rootElement.remove();
        return this;
    },

    wait(..._promises) {
        Promise.all(_promises).then(() => {
            this.close();
        });
    }
});

export default WaitTip;