import { default as tip } from "../submodules/toolkits.js/src/tip";
import AppWorker from "./client/appWorker";
import WelcomeApp from "./client/welcomeApp";
import PackageManager from "./client/packageManager";
import WaitTip from "./client/waitTip";

function pwaMain() {
    window["$aw"] = AppWorker;
    window["$wa"] = WelcomeApp;
    
    console.log("$[PWA Start]");
    window["$tip"] = tip;
    window["$tip"]["WaitTip"] = WaitTip;
    WaitTip();
    WelcomeApp().refresh();
    window["$package"] = PackageManager();
}

AppWorker.start("./sw.js")
    .then(pwaMain)
    .catch(function(error) {
        console.error("$[Service worker registration failed]", error);
    });
