import JSZip from "jszip";
import { singletonClass } from "../../submodules/toolkits.js/src/classDefiner";
import popup from "../../submodules/toolkits.js/src/tip/popup";
import tip from "../../submodules/toolkits.js/src/tip/tip";
import input from "../../submodules/toolkits.js/src/tip/input";
import { URLParams } from "../../submodules/toolkits.js/src/urlParams";
import AppWorker from "./appWorker";
import WelcomeApp from "./welcomeApp";
import WaitTip from "./waitTip";

const $pkgRec = Symbol("package.manager.private.record");

const reloadMenuItem = {
    text: "Reload App",
    action: "reload"
}

const homeMenuItem = {
    image: "data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iOTYiIGhlaWdodD0iOTYiIHhtbG5zPSJodHRwOi8vd3d3LnczLm9yZy8yMDAwL3N2ZyI+PHBhdGggZD0iTTc3LDUyTDQ4LDI0TDEzLDU3TDgsNTJMNDgsMTRMODcsNTJMODIsNTdMNzcsNTJ2MzNoLTIzdi0yNGgtMTJ2MjRoLTIzdi0zMyIgc3Ryb2tlPSIjMDAwIiBzdHJva2Utd2lkdGg9IjMiIGZpbGw9Im5vbmUiIC8+PC9zdmc+",
    imageInvert: true,
    text: "Home Page"
};

const PackageManager = singletonClass("PackageManager", {
    $constructor() {
        this.$menuList = [];
        let btnElement = document.querySelector(".--app-home-button");
        btnElement && popup.bindElement(btnElement, "click", this.onMenuInvoke.bind(this), this.$menuList, { position: "right-align", customTag: "d-app-menu-button" });
        btnElement = document.querySelector(".--app-return-button");
        btnElement && btnElement.addEventListener("click", function () {
            history.back();
        });
        this.$menuBar = document.querySelector(".--app-menu-bar");
        this.$ob = new MutationObserver(this.onDomChanged.bind(this));
        this.$ob.observe(document.body, {
            childList: true,
            attributes: true,
            subtree: true
        });
        this.freshList();
        this._$_default_();
    },

    current: {
        get () {
            return this[$pkgRec];
        }
    },

    currentPath: {
        get () {
            return this[$pkgRec] ? `/.pwa.vir/${this[$pkgRec]}` : ".";
        }
    },

    onDomChanged() {
        if (!this.$menuBar.isConnected) {
            document.body.insertAdjacentElement("afterbegin", this.$menuBar);
        }
    },

    async freshList() {
        const menuList = this.$menuList;
        menuList.splice(0, menuList.length);

        try {
            let response = await fetch("./pkgs/context.json");
            let packageList = await response.json();
            if (packageList instanceof Array) {
                menuList.splice(0, 0, ...packageList);
            } else {
                throw new Error("Package context must be an array");
            }
        } catch(error) {
            console.error("$[PackageManager] Fail in load package list", error);
        }

        (menuList.length > 0) && menuList.push(null);
        menuList.splice(menuList.length, 0, homeMenuItem, null, reloadMenuItem);
    },

    onMenuInvoke(_index) {
        const menuItem = this.$menuList[_index];
        if (menuItem) {
            if (undefined === menuItem.action) {
                this._$_default_();
            } else {
                const action = `_$_${menuItem.action}_`;
                if (typeof this[action] === "function") {
                    WaitTip().show("Loading...").wait(new Promise(r => setTimeout(r, 1000)), this[action](menuItem));
                } else {
                    tip(`"${menuItem.text || String(menuItem)}" unknown!`, { type: "error", closable: true, timeout: 1700 });
                }
            }
        }
    },

    _$_default_() {
        let params = new URLParams(location.search.substr(1));
        if (params.has("pkg")) {
            WaitTip().show("Loading...").wait(new Promise(r => setTimeout(r, 1000)),this._$_package_({ package: params.get("pkg") }));
        } else {
            WelcomeApp().show();
        }
    },

    async _$_reload_(_menuItem) {
        let ret = await AppWorker.sendMessage("reload-application");
        if (ret) {
            if (0 === ret.ret) {
                location.reload();
            } else {
                tip(`Fail in reloading application(${ret.ret})!`, { type: "error", closable: true, timeout: 1700 });
            }
        }
    },

    async _$_package_(_menuItem) {
        let lastError = undefined;
        let context = undefined;

        if (!context) {
            try {
                context = await fetch(`/.pwa.vir/${_menuItem.package}/context.json`);
                context = await context.json();
            } catch (error) {
                context = undefined;
                //lastError = error;
            }
        }

        if (!context) {
            try {
                context = await fetch(`./pkgs/${_menuItem.package}.zip`);
                context = await context.blob();
                let pkg = await (new JSZip()).loadAsync(context);
                context = JSON.parse(await pkg.file("context.json").async("string"));
                ((new URLParams(location.search.substr(1))).get("pkg") === _menuItem.package) 
                    && (context.keepAlive = true);

                let keyCode = undefined;
                if (context.crypt) {
                    let keyHash = undefined;
                    do {
                        keyCode = await input("Input the access code:", "");
                        keyHash = CryptoJS.MD5(keyCode).toString();
                        if (keyCode && (keyHash !== context.crypt)) {
                            tip("The access key is incorrect!", { type: "error", closable: true, timeout: 1000 });
                            keyHash = undefined;
                        }
                    } while (keyCode && (!keyHash));
                    if (!keyCode) {
                        throw new Error("Need an access code");
                    }
                }

                let mapTree = {};
                pkg.forEach((relPath, file) => {
                    if (!file.dir) {
                        mapTree[`${context.keepAlive ? ":" : ""}${_menuItem.package}/${relPath}`] = String(relPath);
                    }
                });
                for (let item in mapTree) {
                    let relPath = mapTree[item];
                    if (relPath.endsWith(".html") || relPath.endsWith(".htm") || relPath.endsWith(".js") || relPath.endsWith(".css")) {
                        mapTree[item] = new Blob([
                            CryptoJS.AES.decrypt(
                                await pkg.file(mapTree[item]).async("string"), 
                                keyCode
                            ).toString(CryptoJS.enc.Utf8)
                        ]);
                    } else {
                        mapTree[item] = await pkg.file(mapTree[item]).async("blob");
                    }
                }
                context.keepAlive || (await AppWorker.sendMessage("clear-virtual-response", { keepAlive: false }));
                let mapRet = await AppWorker.sendMessage("map-virtual-response", mapTree);
                if (0 !== mapRet.ret) {
                    throw new Error(`Fail in mapping virtual response(${mapRet.ret})`);
                }
            } catch (error) {
                context = undefined;
                lastError = error;
            }
        }

        if (context) {
            try {
                ((new URLParams(location.search.substr(1))).get("pkg") === _menuItem.package) 
                        && (context.keepAlive = true);
                let entryFile = await fetch(`/.pwa.vir/${_menuItem.package}/${context.entry}`);
                entryFile = await entryFile.text();
                const container = document.querySelector(".--app-container");
                
                WelcomeApp().hide();
                container.innerHTML = "";
                container.insertAdjacentHTML("beforeend", entryFile);
                let entryFolder = (String(context.entry).substr(0, String(context.entry).lastIndexOf("/")) || ".");

                // update meta,link,style
                [...container.querySelectorAll("meta,link,style")].map(oriItem => {
                    try {
                        oriItem.remove();
                        let newItem = document.createElement(oriItem.tagName);
                        oriItem.getAttributeNames().forEach(attr => newItem.setAttribute(attr, oriItem.getAttribute(attr)));
                        let filePath = String(newItem.getAttribute("href") || "");
                        if (filePath) {
                            if ((filePath.indexOf("://") < 0) && (!filePath.startsWith("/"))) {
                                newItem.setAttribute("href", `/.pwa.vir/${_menuItem.package}/${entryFolder}/${filePath}`);
                            }
                        }
                        newItem.innerHTML = oriItem.innerHTML;
                        return newItem;
                    } catch (error) {
                        lastError = error;
                    }
                }).reverse().forEach(item => {
                    try {
                        item && container.insertAdjacentElement("afterbegin", item);
                    } catch(error) {
                        lastError = error;
                    }
                });

                // update script
                [...container.querySelectorAll("script")].map(oriItem => {
                    try {
                        oriItem.remove();
                        let newItem = document.createElement("script");
                        oriItem.getAttributeNames().forEach(attr => newItem.setAttribute(attr, oriItem.getAttribute(attr)));
                        let filePath = String(newItem.getAttribute("src"));
                        if (filePath) {
                            if ((filePath.indexOf("://") < 0) && (!filePath.startsWith("/"))) {
                                newItem.setAttribute("src", `/.pwa.vir/${_menuItem.package}/${entryFolder}/${filePath}`);
                            }
                        }
                        newItem.innerHTML = oriItem.innerHTML;
                        return newItem;
                    } catch (error) {
                        lastError = error;
                    }
                }).forEach(item => {
                    try {
                        item && container.append(item);
                    } catch(error) {
                        lastError = error;
                    }
                });

                this[$pkgRec] = _menuItem.package;
            } catch(error) {
                lastError = error;
                context = undefined;
            }
        }

        context ? (lastError ? console.warn(`Load "${_menuItem.package}" finished with exception`, lastError) : console.log(`Load "${_menuItem.package}" success`))
                : (tip(`Fail in loading package! ${lastError ? `\n${lastError.message}` : ""}`, { type: "error", closable: true, timeout: 1000 }), 
                    console.error(`Load "${_menuItem.package}" failed`, lastError)
                  );
    }
});

export default PackageManager;
